*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}

input{
  color:blue;
  width: 100%;
  font-size: 1.2rem;
}

.card{
  align-content: center;
  border:1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;

}
.card p{
  text-align: center;
  
}

.card img{
  max-height: 170px;
}

.card-grid{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

}